.App {
    font-family: 'Poppins', sans-serif;
}

::-webkit-scrollbar {
    width: 0;
}

@keyframes fade-in {
    0%{
        opacity: 0;
        transform: translateX(-400px);
    }
    100%{
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes fade-out {
    0%{
        opacity: 1;
        transform: translateX(0);
    }
    100%{
        opacity: 0;
        transform: translateX(100%);
    }
}